.cover {
  background: url("../../../build/images/cover-inicio.webp") no-repeat top center;
  background-size: cover;
  height: 100%;
  width: 100%;
  padding: 300px;
  position: relative;
}
.cover::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: "";
  background-color: rgba(0, 0, 0, 0.4);
}
.cover__title {
  z-index: 1;
  position: relative;
  text-align: center;
  color: #fff;
}

@media all and (min-width: 400px) {
  .cover {
    padding: 100px 0;
  }
}